import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import $ from 'jquery';
import ReactFCCtest from 'react-fcctest';

let quotes =[];
let getQuotes = () =>fetch("https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json").then(response => response.json()).then(data => quotes = data.quotes);
let getRandomColor = () => "#" + ((1 << 24) * Math.random() | 0).toString(16);


class App extends React.Component{
    constructor(props){
        super(props);
        // getQuotes();
        this.state ={
            text: "Wating to load quote",
            author: "...."
        }
        this.changeQuote = this.changeQuote.bind(this);
        console.log(quotes);
    }
    componentDidMount(){
        getQuotes().then(()=>this.changeQuote());
        const link = document.createElement("link");
        link.rel="stylesheet";
        link.href = "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css";
        document.body.appendChild(link);
    }
    changeQuote(){
        let quote = quotes[Math.floor(Math.random()*quotes.length)]
        this.setState({
            text: quote.quote,
            author: quote.author
        });
        let randomColor = getRandomColor();
        $(":root").css('--randomColor', randomColor);
    }
    render(){
        return (
        <div id="quote-box">
            <ReactFCCtest/>
            <p id="text"><i className="fa fa-quote-left"></i> {this.state.text}</p>
            <p id="author">{this.state.author}</p>
            <div id="buttons">
                    <a id="tweet-quote" href="twitter.com/intent/tweet" target="_blank"><i className="fa fa-twitter"></i></a>
            <button id="new-quote" onClick={this.changeQuote}>New Quote</button>
            </div>
        </div>
        );
    }
}
ReactDOM.render(
    <App/>,document.getElementById('root'));